import React, { Component } from 'react';
import Header from '../../components/Header/Header';
import InspectionListContainer from '../../containers/InspectionListContainer'
class InspectionList extends Component{
    constructor() {
        super();
        this.state = {
          open:false,
        };
        
    }
    handleOpenNavBar = () => {
        this.setState({open:!this.state.open})
    }
    
    render() {
        return (
            <div>
                <div>
                    <div id="loadListInspection" className="wrapper">
                        <div id="st-container" className={this.state.open ? "st-container st-effect-1 st-menu-open" : "wrapper dashboard-wrapper st-container"}>
                            <Header
                                header="Brand Inspection listing"
                                openNavBar={this.handleOpenNavBar}
                                open={this.state.open} />
                            <div className="main-middle-area">
                                <section className="table-listing-section">
                                    <div className="listing-inner">
                                        <div className="container">
                                            <InspectionListContainer />
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default InspectionList;