import React, { Component } from 'react';
import '../../assets/css/listing-table.css'
import InfiniteScroll from "react-infinite-scroll-component";
import { getUrl } from '../../services/network/urls';
import {  get } from '../../services/network/requests';
import { Link } from 'react-router-dom'

class InspectionListComponent extends Component{
    constructor(props) {
        super(props);
        this.state = {
            
            loading: true,
            inspectionRecords:[],
            items: Array.from({ length: 20 }),
            page : 1,
            search : '',
            searchText : '',
            lastPage : 1
        } 
    }
    async componentDidMount() {
        await this.getListforLiveStcok()
    }

    getListforLiveStcok = () => {
        let page = this.state.page
        let search =  this.state.search;
        if(page <= this.state.lastPage){
        let url = getUrl('get_live_stock_list')+'?page='+page+'&search='+search
        get(url)
            .then(res => {
                const { data: { code, data, status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            let oldData = this.state.inspectionRecords;
                            if(page === 1){
                                oldData = []
                            }
                            let newData = data.result
                            this.setState({
                                inspectionRecords: [...oldData, ...newData],
                                lastPage:data.links.last,
                                loading:false
                            })
                        }
                        break;
                    default:
                        break;
                }
            })
            .catch(error => console.log(error))
        }else{
            this.setState({
                loading: false
            })
        }

    }

    loadMore=()=> {
        console.log("loadMore")
        this.setState({
            page :this.state.page + 1,
            loading:true
        },() => {
                this.getListforLiveStcok()
        })
    }

    handleSearchTextBox = (e) => {
        this.setState({
            searchText : e.target.value
        })
    }

    handleSearch = () => {
        this.setState({
            search: this.state.searchText,
            page:  1,
            loading: true
        }, () => {
            this.getListforLiveStcok()
        })
    }


    render() {
        return (
            <React.Fragment>
                <div className="row justify-content-end">
                    <div className="col-lg-4 col-md-6"> 
                        <div className="form-group form-search-icon">
                            <input className="form-control " onChange={this.handleSearchTextBox}/>
                            <span className="icon-search" onClick={this.handleSearch}><i className="fe fe-search"></i></span>
                        </div>
                    </div>
                </div>
                <div className="row" ref="myscroll">
                    <div className="col-lg-12" >
                        <div className="table-responsive">
                            <InfiniteScroll
                                dataLength={this.state.inspectionRecords.length}
                                next={this.loadMore}
                                hasMore={this.state.page < this.state.lastPage}
                                loader={<h4>Loading...</h4>}
                            >
                            <table className="table table-listing">
                                <thead>
                                    <tr>
                                        <th>ID</th>
                                        <th>Owner</th>
                                        <th>Buyer</th>
                                        <th>Destination</th>
                                        <th>Status</th>
                                        <th>Price</th>
                                    </tr>
                                </thead>
                                <tbody>
                                {this.state.inspectionRecords.map((item, index) => {
                                    return (
                                        <tr key={'list' + index}>
                                            <td className="text-bold">{index+1}</td>
                                            <td>{item.owner}</td>
                                            <td>{item.buyer}</td>
                                            <td>{item.destination}</td>
                                            <td className={!item.draft ? "text-green" : "text-red"}>{!item.draft ? 'Complete' : <Link to={`/livestock/edit/${item.id}`}>Incomplete</Link>}</td>
                                            <td className="text-bold">$ 0.00 {/* {item.price} */}</td>
                                        </tr>
                                    )
                                })}
                                    </tbody>
                                </table>
                            </InfiniteScroll>
                        </div>
                    </div>
                </div>
                
            </React.Fragment>
        )
    }
}
export default InspectionListComponent;